const dropdown = document.querySelectorAll(".custom-dropdown .dropdown");

if (dropdown) {
    dropdown.forEach((item, index) => {
        const dropdownPlaceholder = item.querySelector(".placeholder"),
            dropdownPlaceholderText = dropdownPlaceholder.querySelector(".placeholder-text"),
            dropdownList = item.querySelector(".dropdown-list"),
            listHeight = dropdownList.offsetHeight;

        dropdownList.style.top = dropdownPlaceholder.offsetHeight + "px";
        item.addEventListener("click", (e) => {
            item.classList.toggle("active");

            if (item.classList.contains("active")) {
                dropdownList.style.height = listHeight + "px";
            } else {
                dropdownList.style.height = 0;
            }
        })
        dropdownList.querySelectorAll("ul li").forEach(listItem => listItem.addEventListener("click", () => {
            dropdownPlaceholderText.innerHTML = listItem.querySelector("div").innerHTML
        }))
    })
}


// Home page map
const countries = document.querySelectorAll("section.map-area svg > path"),
    svgContainer = document.querySelector("section.map-area .svg-map");
if (countries && svgContainer) {
    window.addEventListener("DOMContentLoaded", () => {
        const countryTab = document.querySelectorAll(".map-area .tab"),
            tabContent = document.querySelectorAll(".map-area .main-content"),
            tabContentDesktop = document.querySelectorAll(".map-area .main-content-desktop"),
            projectPageTabContent = document.querySelectorAll(".toggle-map-content-area .toggle-map-content"),
            svg = svgContainer.querySelector("svg"),
            prevTab = document.querySelector(".map-area .prev-tab"),
            nextTab = document.querySelector(".map-area .next-tab");

        // lgili alan mobilde slider olaca iin masastnde gzkecek olan ierikleri kaldryoruz
        if (windowWidth < mobileLimit) {
            tabContentDesktop.forEach(content => {
                content.remove()
            })
        }

        countries.forEach((item, index) => {
            const itemRect = item.getBoundingClientRect(),
                content = document.querySelectorAll(".map-area .content")[index];

            // Tm pathelere yani tm lkelere id atamas yapyoruz
            item.setAttribute("data-country-id", `${index}`);

            if (content) {
                const dotRect = content.getBoundingClientRect();

                // Noktalarn pozisyonlarn belirliyoruz
                content.style.left = `${(itemRect.x - dotRect.x) + itemRect.width / 2 - dotRect.width / 2}px`;
                content.style.top = `${(itemRect.y - dotRect.y) + itemRect.height / 2 - dotRect.height / 2}px`;

                // Projeye sahip olan lkeye class atyoruz
                item.classList.add("has-project")
                const addHover = () => {
                    content.classList.add("hovered")
                    item.classList.add("hovered")
                }
                const removeHover = () => {
                    content.classList.remove("hovered")
                    item.classList.remove("hovered")
                }

                // hover olma durumunda lkeye ve noktaya class atayp kaldryoruz
                content.addEventListener("mouseenter", addHover)
                content.addEventListener("mouseleave", removeHover)
            }
        })

        // Tab Events
        function projectTab(ev, tabID) {
            let i;

            for (i = 0; i < countryTab.length; i++) {
                countryTab[i].classList.remove("active")
            }

            for (i = 0; i < tabContent.length; i++) {
                tabContent[i].classList.remove("active")
                tabContent[i].classList.add("non-swiper-slide")
            }

            ev.currentTarget.classList.add("active")
            document.querySelectorAll(`.map-area [data-content-id='${tabID}']`).forEach(item => {
                item.classList.add("active")
                item.classList.remove("non-swiper-slide")
            })
        }

        function projectPageTab(ev, tabID) {
            let i;

            for (i = 0; i < countryTab.length; i++) {
                countryTab[i].classList.remove("active")
            }

            for (i = 0; i < projectPageTabContent.length; i++) {
                projectPageTabContent[i].classList.remove("active")
            }

            ev.currentTarget.classList.add("active")
            document.querySelectorAll(`.map-area [data-content-id='${tabID}']`).forEach(item => {
                item.classList.add("active")
            })
        }

        function showAllProjects() {

            for (let s = 0; s < tabContent.length; s++) {
                tabContent[s].classList.add("active")
            }

            for (let x = 0; x < projectPageTabContent.length; x++) {
                projectPageTabContent[x].classList.add("active")
            }
        }

        let p = 0, n = 1;
        const isMobile = windowWidth < mobileLimit
        countryTab.forEach((tab, index) => {
            // ncelikle "All Projects" seenei aktif olaca iin fonksiyonu direkt altryoruz
            showAllProjects()
            const tabID = tab.getAttribute("data-tab-id");
            if (tabID === "0") {
                showAllProjects()
            }

            const tabContainer = document.getElementById("tab-container")

            tab.addEventListener("click", (e) => {
                let b;

                projectMobileSlider.destroy()
                tabContent.forEach(item => {
                    const all = item
                    const allContentID = all.getAttribute("data-content-id")
                    if (isMobile) {
                        if (tabID !== "0") item.remove();
                    }

                    const contentID = item.getAttribute("data-content-id");
                    if (document.querySelector(".custom-dropdown .placeholder span")) {
                        document.querySelector(".custom-dropdown .placeholder span").innerText = tab.firstElementChild.innerText
                    }



                    if (tabID === contentID && isMobile) {
                        document.querySelector(".project-mobile-slider .swiper-wrapper").insertAdjacentElement("afterbegin", item)
                        projectTab(e, `${contentID}`);
                        setTimeout(() => {
                            let a = new Swiper(".project-mobile-slider", projectMobileSliderParams)
                            a.slideTo(0, 1000, true)
                            a.update()
                        }, 300)

                    } else if (tabID === contentID && !isMobile) {
                        projectTab(e, `${contentID}`);
                    }

                    if (tabID === "0" && isMobile) {
                        console.log("aa")
                        document.querySelector(".project-mobile-slider .swiper-wrapper").insertAdjacentElement("afterbegin", all)
                        e.currentTarget.classList.add("active")
                        document.querySelector(`.map-area [data-content-id='${allContentID}']`).classList.add("active")
                        document.querySelector(`.map-area [data-content-id='${allContentID}']`).classList.remove("non-swiper-slide")
                        b = new Swiper(".project-mobile-slider", projectMobileSliderParams)
                        b.slideTo(0, 1000, true)
                        b.update();
                        b.updateSlides();
                    }
                })

                projectPageTabContent.forEach(item => {
                    if (tabID === "0") {
                        showAllProjects()
                    }
                    const contentID = item.getAttribute("data-content-id");

                    if (tabID === contentID) projectPageTab(e, `${contentID}`);
                })

                if (windowWidth < 1280) {
                    let scrollPos = tab.offsetLeft - (tabContainer.offsetWidth / 2 - tab.offsetWidth / 2)
                    tab.parentElement.parentElement.scrollTo({
                        left: scrollPos,
                        behavior: "smooth"
                    })
                }
            })
        })

        // Zoom in - zoom out
        let zoomIn = document.querySelector(".zoom-in"),
            zoomOut = document.querySelector(".zoom-out"),
            scaleObject = svgContainer.querySelector(".svg-map-scale"),
            scaleValue = parseInt(scaleObject.getAttribute("data-scale")),
            svgWrapper = scaleObject.querySelector(".svg-wrapper"),
            overlay = document.querySelector(".description-overlay");

        scaleObject.style.transform = `scale(${scaleValue}%)`;

        let i = 0,
            maxZoom = parseInt(scaleObject.getAttribute("data-max-zoom")) || 20,
            zoomRatio =
                parseInt(scaleObject.getAttribute("data-zoom-ratio")) || 60,
            x = maxZoom;

        zoomIn.addEventListener("click", (e) => {
            i++;
            x++;
            zoomOut.classList.add("active");
            if (i === maxZoom) {
                scaleObject.style.transform = `scale(${(scaleValue +=
                    zoomRatio)}%)`;
                zoomIn.classList.remove("active");
            } else {
                scaleObject.style.transform = `scale(${(scaleValue +=
                    zoomRatio)}%)`;
            }

            if (i === 1) {
                overlay.classList.add("active");
                setTimeout(() => {
                    overlay.classList.remove("active");
                }, 1500);
            }
        });
        zoomOut.addEventListener("click", (e) => {
            x--;
            i--;
            if (i === 0) {
                scaleObject.style.transform = `scale(${(scaleValue -=
                    zoomRatio)}%)`;
                zoomOut.classList.remove("active");
            } else if (i === -1) {
                scaleObject.style.transform = `scale(${(scaleValue -=
                    zoomRatio)}%)`;
                console.log("-1 oldu");
            } else {
                if (scaleObject.style.transform === "scale(1)") {
                    zoomOut.classList.remove("active");
                } else {
                    scaleObject.style.transform = `scale(${(scaleValue -=
                        zoomRatio)}%)`;
                    zoomIn.classList.add("active");
                }
            }

            if (i === 1) {
                overlay.classList.remove("active");
            }
        });

        // Mouse'un alandan ktn yakalayan event
        svgContainer.addEventListener("mouseleave", (e) => {
            svgWrapper.classList.add("duration-300");
            setTimeout(() => {
                svgWrapper.classList.remove("duration-300");
            }, 300);

            const pX = e.clientX,
                pY = e.clientY,
                a = document.querySelector("a[data-project-fancybox]");

            if (scaleObject.style.transform !== "scale(1)") {
                if (a && (e.target === a || a.contains(e.target))) {
                    svgWrapper.style.transform = `translate(${-pX - svgContainer.offsetWidth
                        }px, ${-pY - svgContainer.offsetHeight}px)`;
                } else {
                    svgWrapper.style.transform = `translate(0, 0)`;
                }
            }
        });

        // Project page switch
        const mapSwitch = document.querySelector(".map-switch")
        if (mapSwitch) {
            const switcher = mapSwitch.querySelector(".switcher"),
                toggleMap = document.querySelector(".toggle-map")

            switcher.addEventListener("click", () => {
                mapSwitch.classList.toggle("active")
                toggleMap.classList.toggle("active")
            })

            if (document.querySelector(".index-page")) {
                mapSwitch.remove();
            }
        }




        // Deikenlerin tanmlanmas
        let isDragging = false; // Srkleme durumu
        let mouseX, mouseY; // Mouse konumu
        let lastX, lastY; // Son konum
        let svgCenterX, svgCenterY; // SVG'nin merkezi

        // SVG'nin ortasn bulma fonksiyonu
        function findSvgCenter() {
            const svgRect = svgContainer.getBoundingClientRect();
            svgCenterX = svgRect.left + svgRect.width / 2;
            svgCenterY = svgRect.top + svgRect.height / 2;
        }

        // Mouse'un basl tutulduu durumu yakalayan event
        svgContainer.addEventListener("mousedown", (e) => {
            isDragging = true;
            findSvgCenter(); // SVG'nin ortasn bul
            mouseX = e.clientX;
            mouseY = e.clientY;
            lastX = svgWrapper.style.transform
                ? parseFloat(
                    svgWrapper.style.transform.split("(")[1].split("px")[0]
                )
                : 0;
            lastY = svgWrapper.style.transform
                ? parseFloat(
                    svgWrapper.style.transform.split(",")[1].split("px")[0]
                )
                : 0;
        });

        // Mouse'un brakld durumu yakalayan event
        svgContainer.addEventListener("mouseup", () => {
            isDragging = false;
        });

        // Mouse'un hareket ettii durumu yakalayan event
        svgContainer.addEventListener("mousemove", (e) => {
            // Eer srkleme durumu aktifse
            if (isDragging && scaleObject.style.transform !== "scale(1)") {
                // Yeni mouse konumunu al
                const deltaX = e.clientX - mouseX;
                const deltaY = e.clientY - mouseY;

                // Harita konumunu gncelle
                svgWrapper.style.transform = `translate(${deltaX + lastX}px, ${deltaY + lastY
                    }px)`;

                // Son konumu gncelle
                lastX += deltaX;
                lastY += deltaY;

                // Mouse'un konumunu gncelle
                mouseX = e.clientX;
                mouseY = e.clientY;
            }
        });

        // Mouse'un alandan ktn yakalayan event
        svgContainer.addEventListener("mouseleave", () => {
            isDragging = false;
        });

        // SVG yklendiinde SVG'nin ortasn bul
        window.addEventListener("load", findSvgCenter);

        zoomIn.addEventListener("click", (e) => {
            // Dier kodlar buraya
            svgContainer.classList.add("grab"); // Zoom yaparken cursoru "grab" olarak deitir
        });

        zoomOut.addEventListener("click", (e) => {
            // Dier kodlar buraya
            if (scaleObject.style.transform === "scale(1)") {
                svgContainer.classList.remove("grab"); // Zoom yaparken cursoru "grab" olarak deitir
            }
        });
        // basl tuttuunda cursoru "grabbing" yap
        svgContainer.addEventListener("mousedown", () => {
            // grab varsa 
            if (svgContainer.classList.contains("grab")) {
                svgContainer.classList.add("grabbing");
            }
        });
        // braktnda cursoru "grab" yap
        svgContainer.addEventListener("mouseup", () => {
            svgContainer.classList.remove("grabbing");
        });
    });
}





