if (document.querySelector(".page-about-tabs")) {
    // .page-about-tab alanna gelince .f-nav absolute class kar ve f-nav alan fixed class eklenir
    let fNav = document.querySelector(".f-nav");
    let pageAboutTab = document.querySelector(".page-about-tabs");
    let pageAboutTabOffsetTop = pageAboutTab.offsetTop;
    window.addEventListener("scroll", function () {
        if (window.scrollY > pageAboutTabOffsetTop && windowWidth > mobileLimit) {
            fNav.classList.remove("absolute");
            fNav.classList.add("fixed");
            header.classList.add("is-hidden");
        } else {
            fNav.classList.remove("fixed");
            fNav.classList.add("absolute");
        }
    });

    // .tabs ul li a data-target alanlarna scroll yaplr ve active class eklenir
    document.addEventListener("DOMContentLoaded", function () {
        let tabs = document.querySelectorAll(".tabs ul li a");
        let menuCenterItems = document.querySelectorAll(
            "#menu-center ul li .tab-scrollable"
        );

        let tabStates = {};
        let currentActiveTab = null;
        let windowWidth = window.innerWidth; // Assuming you have windowWidth defined

        tabs.forEach((tab) => {
            tabStates[tab.getAttribute("data-target")] = {
                element: tab,
            };

            tab.addEventListener("click", function (e) {
                e.preventDefault();
                let dataTarget = this.getAttribute("data-target");
                let target = document.querySelector(dataTarget);

                // Use GSAP to scroll to the target
                gsap.to(window, {
                    duration: 1,
                    scrollTo: { y: target, offsetY: -20 },
                    onComplete: function () {
                        tabs.forEach((otherTab) => {
                            otherTab.classList.remove("active");
                        });
                        tab.classList.add("active");
                        currentActiveTab = tab;
                        updateMapListPosition();
                    },
                });
            });
        });

        let isScrolling = false;

        window.addEventListener("scroll", function () {
            if (!isScrolling) {
                isScrolling = true;

                // Use a debounce timer to limit the frequency of scroll event handling
                setTimeout(function () {
                    let currentScrollPosition = window.scrollY;

                    for (const [dataTarget, state] of Object.entries(
                        tabStates
                    )) {
                        let target = document.querySelector(dataTarget);
                        if (target) {
                            if (currentScrollPosition > target.offsetTop - 30) {
                                tabs.forEach((tab) => {
                                    tab.classList.remove("active");
                                });
                                state.element.classList.add("active");
                                currentActiveTab = state.element;
                                updateMapListPosition();
                            }
                        }
                    }

                    clearTimeout(window.scrollTimeout);
                    window.scrollTimeout = setTimeout(function () {
                        // Update mapList position after scrolling is completed
                        updateMapListPosition();
                        isScrolling = false;
                    }, 500); // Adjust the delay according to your needs
                }, 0); // Adjust the debounce time according to your needs
            }
        });
        function updateMapListPosition() {
            let fnav = document.querySelector(".fnav");
            if (
                fnav &&
                fnav.classList.contains("fixed") &&
                windowWidth < 1024
            ) {
                let clickedButtonWidth = currentActiveTab.offsetWidth;
                let clickedButtonPosition = currentActiveTab.offsetLeft;
                let mapList = document.querySelector(".tabs-scroll");
                mapList.scrollLeft =
                    clickedButtonPosition -
                    (mapList.offsetWidth - clickedButtonWidth) / 2;
            }
        }

        if (windowWidth < 1024) {
            let mapList = document.querySelector(".tabs-scroll");
            let mapBox = document.querySelectorAll(".tab-scrollable");

            let adjustScroll = (element) => {
                let clickedButtonWidth = element.offsetWidth;
                let clickedButtonPosition = element.offsetLeft;
                mapList.scrollLeft =
                    clickedButtonPosition -
                    (mapList.offsetWidth - clickedButtonWidth) / 2;
            };

            mapBox.forEach((element) => {
                element.addEventListener("click", () => {
                    adjustScroll(element);
                });
            });

            let observer = new MutationObserver((mutations) => {
                mutations.forEach((mutation) => {
                    if (
                        mutation.type === "attributes" &&
                        mutation.attributeName === "class"
                    ) {
                        let target = mutation.target;
                        if (target.classList.contains("active")) {
                            adjustScroll(target);
                        }
                    }
                });
            });

            mapBox.forEach((element) => {
                observer.observe(element, { attributes: true });
            });
        }
    });
}



// Corporate page fixed bar
const fixedBar = document.querySelector(".fixed-bar");
const fixedWrapper = document.querySelector(".fixed-wrapper")
const breadcrumb = document.getElementById("breadcrumb")

if (document.querySelector("main").classList.contains("about-us-page") && fixedBar) {

    window.addEventListener("scroll", () => {
        if (window.scrollY > breadcrumb.offsetHeight) {
            fixedBar.classList.add("is-fixed")
        } else {
            fixedBar.classList.remove("is-fixed")
        }

        if (footer.getBoundingClientRect().top < fixedBar.offsetHeight) {
            fixedBar.classList.remove("is-fixed")
        }
    })

    const items = fixedBar.querySelectorAll(".item");

    // About us page section grnr olduunda fixed olan barda ilgili item aktif olacak.
    const calcRect = (section, item, itemID, sectionID) => {
        const rect = section.getBoundingClientRect();

        const sectionVisible = rect.top <= section.clientHeight / 2
        const isBelowTop = rect.top >= -section.clientHeight + fixedBar.offsetHeight;
        if (sectionVisible) {
            section.classList.add("active")

            if (itemID === sectionID) {
                item.classList.add("active")
            } else {
                item.classList.remove("active")
            }
        }

        if (!isBelowTop) {
            section.classList.add("is-below-top")
            section.classList.remove("active")
        }
    };

    function removeActive(ev) {
        for (let i = 0; i < items.length; i++) {
            items[i].classList.remove("active")
        }

        ev.currentTarget.classList.add("active")
    }

    items.forEach(item => {
        item.addEventListener("click", (e) => {
            removeActive(e)
        })
    })

    window.addEventListener("scroll", () => {
        items.forEach(item => {
            const itemID = item.getAttribute("data-section-id"),
                sections = document.querySelectorAll(".about-us-page .section");
            sections.forEach(section => {
                const sectionID = section.getAttribute("data-section-id")
                calcRect(section, item, itemID, sectionID)
            })
        })
    })
}

// Ekran genilii 1280 pikselden kkse ve sadece "about-us-page" snf mevcutsa alacak koul
if (window.innerWidth <= 1280 && document.querySelector("main").classList.contains("about-us-page")) {
    window.addEventListener("scroll", () => {
        // Kaydrma ilemini gerekletir
        let scrollPos = fixedBar.offsetLeft - (fixedBar.parentElement.offsetWidth / 2 - fixedBar.offsetWidth / 2);
        fixedBar.parentElement.scrollTo({
            left: scrollPos,
            behavior: "smooth"
        });

        // Dier scroll olaylarn buraya ekleyebilirsiniz...
    });

    // Dier kod paralarn buraya ekleyebilirsiniz...             fixedBar
}

const productTabs = document.querySelectorAll(".product-tabs .tab");
const productTabContainer = document.getElementById("product-tab-container");
const aboutTabs = document.querySelectorAll(".page-about-tabs .item");
const barFixed = document.getElementById("fixed-bar");

// Tm tablara tklama olay dinleyicisi ekle
productTabs.forEach(tab => {
    tab.addEventListener('click', () => {
        // Tklanan tab'n ortalanmas iin ilemler

        let scrollPos = tab.offsetLeft - (productTabContainer.offsetWidth / 2 - tab.offsetWidth / 2)
        productTabContainer.scrollTo({
            left: scrollPos,
            behavior: "smooth"
        })
    });
});

aboutTabs.forEach(tab => {
    tab.addEventListener('click', () => {
        // Tklanan tab'n ortalanmas iin ilemler

        let scrollPos = tab.offsetLeft - (barFixed.offsetWidth / 2 - tab.offsetWidth / 2)
        barFixed.firstElementChild.scrollTo({
            left: scrollPos,
            behavior: "smooth"
        })
    });
});








