if (document.querySelector('[gsapSticky]') && window.innerWidth > 1024) {
    const wrapper = document.querySelector('[gsapSticky="wrapper"]');
    const image = document.querySelector('[gsapSticky="image"]');
    const imageEach = image.querySelectorAll('img');
    const each = document.querySelectorAll('[gsapSticky="each"]');
    image.style.transition = 'margin .45s';
    gsap.to(image, {
        scrollTrigger: {
            trigger: image,
            endTrigger: wrapper,
            start: 'top 80px',
            end: `${wrapper.clientHeight + image.clientHeight - 260}px bottom`,
            pin: true,

            onUpdate: () => {
                if (!header.classList.contains('is-hidden')) {
                    console.log('testr');
                    image.style.marginTop = `${header.clientHeight}px`;
                } else {
                    image.style.marginTop = `${0}px`;
                }
            },
            onLeave: () => {
                image.style.marginTop = `${0}px`;
            },
            onLeaveBack: () => {
                image.style.marginTop = `${0}px`;
            }
        }
    })

    function enterEach(i) {
        each.forEach(ee => {
            ee.classList.remove('active');
        });
        each[i].classList.add('active');

        imageEach.forEach(ie => {
            ie.style.opacity = 0;
        });
        imageEach[i].style.opacity = 1;
    }

    each.forEach((e, i) => {
        gsap.to(e, {
            scrollTrigger: {
                trigger: e,
                start: 'top center',
                end: 'bottom center',

                onEnter: () => {
                    enterEach(i);
                },

                onEnterBack: () => {
                    enterEach(i);
                }
            }
        })
    });
}



